import React from 'react';

import cls from './TestFinish.module.css';


const TestFinish = ({ onOnlyFinish, handleFinishAllTest }) => {
    return (
        <div className = {cls.finish}>
            <div className = {cls.finish__inner}>
                <div className = {cls.finish__title}>  ,     ? </div>
                <div className = {cls.finish__result}> : 80/140 </div>
                <div className = {cls.finish__time}>   : 01:59:45 </div>

                <button className = {cls.yes__finish} onClick = { handleFinishAllTest }> ,   </button>
                <button className = {cls.no__finish} onClick = { onOnlyFinish }>  </button>
            </div>
        </div>
    )
}

export default TestFinish;